/* RESET */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* BASE */
body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background: linear-gradient(135deg, #f0fdf4 0%, #ecfdf5 50%, #d1fae5 100%);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-x: hidden;
  color: #111827;
}

/* CLERK ROOT CENTERING */
.cl-rootBox {
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  min-height: 100vh !important;
  width: 100%;
}

/* CLERK CARD OVERRIDE */
.cl-card {
  background: linear-gradient(135deg, #ffffff 0%, #f0fdf4 100%) !important;
  border-radius: 20px !important;
  border: 1px solid rgba(16, 185, 129, 0.2) !important;
  box-shadow: 0 10px 30px rgba(16, 185, 129, 0.15) !important;
  padding: 32px !important;
  max-width: 420px !important;
  width: 100% !important;
}

/* HEADINGS */
.cl-headerTitle {
  color: #065f46 !important; /* dark green */
  font-weight: 700 !important;
}

.cl-headerSubtitle {
  color: #047857 !important;
  font-size: 14px !important;
}

/* INPUTS */
.cl-formFieldInput {
  border-radius: 12px !important;
  border: 2px solid #e5e7eb !important;
  background: #ffffff !important;
  transition: all 0.2s ease !important;
}

.cl-formFieldInput:focus {
  border-color: #10b981 !important;
  box-shadow: 0 0 0 3px rgba(16, 185, 129, 0.2) !important;
}

/* PRIMARY BUTTON */
.cl-formButtonPrimary {
  background: linear-gradient(135deg, #10b981 0%, #34d399 100%) !important;
  border: none !important;
  border-radius: 12px !important;
  padding: 12px 20px !important;
  font-weight: 600 !important;
  font-size: 16px !important;
  color: #fff !important;
  box-shadow: 0 4px 15px rgba(16, 185, 129, 0.3) !important;
  transition: all 0.2s ease !important;
}

.cl-formButtonPrimary:hover {
  transform: translateY(-2px) !important;
  box-shadow: 0 8px 25px rgba(16, 185, 129, 0.4) !important;
}

/* SOCIAL BUTTONS */
.cl-socialButtonsBlockButton {
  border-radius: 12px !important;
  border: 2px solid #e5e7eb !important;
  background: #fff !important;
  transition: all 0.2s ease !important;
}

.cl-socialButtonsBlockButton:hover {
  border-color: #10b981 !important;
  background: #f0fdf4 !important;
}

/* DIVIDER */
.cl-dividerLine {
  background: rgba(16, 185, 129, 0.3) !important;
}

.cl-dividerText {
  color: #065f46 !important;
  font-weight: 500 !important;
}

/* LINKS */
.cl-footerActionLink {
  color: #059669 !important;
  font-weight: 600 !important;
}

.cl-footerActionLink:hover {
  color: #047857 !important;
}
/* Main container -> full screen center */
.auth-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: linear-gradient(135deg, #f0fdf4, #ecfdf5 50%, #d1fae5);
}

/* Card */
.auth-card {
  background: #ffffffee;
  border-radius: 20px;
  padding: 40px 32px;
  width: 100%;
  max-width: 420px;
  box-shadow: 0 20px 40px rgba(16, 185, 129, 0.15);
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: slideUp 0.6s ease-out;
}

/* Header with logo + text */
.auth-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 24px;
  text-align: center;
}

.logo {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 8px;
}

.logo-icon {
  width: 52px;
  height: 52px;
  background: linear-gradient(135deg, #10b981, #34d399);
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  font-size: 18px;
  color: white;
  letter-spacing: -0.02em;
  box-shadow: 0 6px 18px rgba(16, 185, 129, 0.35);
}

.logo h1 {
  font-size: 26px;
  font-weight: 700;
  background: linear-gradient(135deg, #10b981, #059669);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Subtitle under logo */
.auth-subtitle {
  font-size: 15px;
  color: #374151;
  line-height: 1.4;
  font-weight: 400;
}

/* Clerk SignIn content */
.auth-content {
  width: 100%;
  margin: 16px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Footer */
.auth-footer {
  margin-top: 24px;
  padding-top: 16px;
  border-top: 1px solid rgba(16, 185, 129, 0.15);
  text-align: center;
  width: 100%;
}

.auth-footer p {
  font-size: 13px;
  color: #6b7280;
}
